
const myMixin = {
  data() {
    return {
      Mi_isNoData: false,
      Mi_isShowLoding: true,
      Mi_MSG: 'hello'
    };
  },
  created() {},
  mounted() {},
  methods: {
    // 删除数组中某个对象
    removeArray(_arr, _obj) {
      let length = _arr.length;
      for (var i = 0; i < length; i++) {
        if (_arr[i] === _obj) {
          if (i === 0) {
            _arr.shift(); // 删除并返回数组的第一个元素
            return _arr;
          } else if (i === length - 1) {
            _arr.pop(); // 删除并返回数组的最后一个元素
            return _arr;
          } else {
            _arr.splice(i, 1); // 删除下标为i的元素
            return _arr;
          }
        }
      }
    },
    // 解决选择器(Cascader)出现空白选项
    getTreeData(data) {
      // 循环遍历json数据
      for (var i = 0; i < data.length; i++) {
        if (data[i].children.length < 1) {
          // children若为空数组，则将children设为undefined
          data[i].children = undefined;
        } else {
          // children若不为空数组，则继续 递归调用 本方法
          this.getTreeData(data[i].children);
        }
      }
      return data;
    },
    // 验证邮箱格式
    sendEmail(email) {
      let regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (email != '' && !regEmail.test(email)) {
        this.$message({
          message: '邮箱格式不正确',
          type: 'warning'
        });
        email = '';
      }
    },
    // 密码验证
    pwdCheck(password) {
      let reg = /(?!^(\d+|[a-zA-Z]+|[~!@.#$%^&*?]+)$)^[\w~!@.#$%^&*?]{6,16}$/;
      if (!reg.test(password)) {
        this.$message({
          message: '密码格式不正确',
          type: 'warning'
        });
        password = '';
      }
    },
    // 选中操作权限 https://blog.csdn.net/Beam007/article/details/87858291      https://www.jianshu.com/p/4e0ecb0f796d
    clickDeal(currentObj, treeStatus) {
      // 用于：父子节点严格互不关联时，父节点勾选变化时通知子节点同步变化，实现单向关联。
      let selected = treeStatus.checkedKeys.indexOf(currentObj.key); // -1未选中
      // 选中
      if (selected !== -1) {
        // 子节点只要被选中父节点就被选中
        this.selectedParent(currentObj);
        // 统一处理子节点为相同的勾选状态
        this.uniteChildSame(currentObj, true);
      } else {
        // 未选中 处理子节点全部未选中
        if (currentObj.children.length !== 0) {
          this.uniteChildSame(currentObj, false);
        }
      }
    },
    // 统一处理子节点为相同的勾选状态
    uniteChildSame(treeList, isSelected) {
      this.$refs.tree.setChecked(treeList.key, isSelected);
      for (let i = 0; i < treeList.children.length; i++) {
        this.uniteChildSame(treeList.children[i], isSelected);
      }
    },
    // 统一处理父节点为选中
    selectedParent(currentObj) {
      let currentNode = this.$refs.tree.getNode(currentObj);
      if (currentNode.parent.key !== undefined) {
        this.$refs.tree.setChecked(currentNode.parent, true);
        this.selectedParent(currentNode.parent);
      }
    },
    FromDates(StringDate) {
      let T = '';
      let N = '';
      let Y = '';
      let R = '';
      for (let i = 0; i < StringDate.length; i++) {
        N = StringDate.substring(0, 4) + '-';
        Y = StringDate.substring(4, 6) + '-';
        R = StringDate.substring(6, 8);
        T = N + Y + R;
        return T;
      }
    }
  }
};
export default myMixin;
